html {
  font-family: sans-serif;
  text-size-adjust: 100%;
}
body {
  margin: 0;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block;
}
audio,
canvas,
progress,
video {
  display: inline-block;
}
audio:not([controls]) {
  display: none;
  height: 0;
}
progress {
  vertical-align: baseline;
}
template,
[hidden] {
  display: none;
}
a {
  background-color: transparent;
  text-decoration-skip: objects;
}
a:active,
a:hover {
  outline-width: 0;
}
abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}
b,
strong {
  font-weight: inherit;
}
b,
strong {
  font-weight: bolder;
}
dfn {
  font-style: italic;
}
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}
mark {
  background-color: #ff0;
  color: #000;
}
small {
  font-size: 80%;
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sub {
  bottom: -0.25em;
}
sup {
  top: -0.5em;
}
img {
  border-style: none;
}
svg:not(:root) {
  overflow: hidden;
}
code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}
figure {
  margin: 1em 40px;
}
hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}
button,
input,
select,
textarea {
  font: inherit;
  margin: 0;
}
optgroup {
  font-weight: bold;
}
button,
input {
  overflow: visible;
}
button,
select {
  text-transform: none;
}
button,
html [type="button"],
[type="reset"],
[type="submit"] {
  appearance: button;
}
button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}
button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}
fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}
legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}
textarea {
  overflow: auto;
}
[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}
[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}
[type="search"] {
  appearance: textfield;
  outline-offset: -2px;
}
[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  appearance: none;
}
::-webkit-input-placeholder {
  color: inherit;
  opacity: 0.54;
}
::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}
@font-face {
  font-family: "Prata-Regular";
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Prata-Regular.woff2") format("woff2"), url("../fonts/Prata-Regular.woff") format("woff");
}
@font-face {
  font-family: "PlayfairDisplay-Regular";
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/PlayfairDisplay-Regular.woff2") format("woff2"), url("../fonts/PlayfairDisplay-Regular.woff") format("woff");
}
@font-face {
  font-family: "PlayfairDisplay-Bold";
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/PlayfairDisplay-Bold.woff2") format("woff2"), url("../fonts/PlayfairDisplay-Bold.woff") format("woff");
}
@font-face {
  font-family: "Roboto-Medium";
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff");
}
@font-face {
  font-family: "Roboto-Light";
  font-weight: 300;
  font-display: swap;
  src: url("../fonts/Roboto-Light.woff2") format("woff2"), url("../fonts/Roboto-Light.woff") format("woff");
}
@font-face {
  font-family: "Roboto-Regular";
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff");
}
.container {
  width: 1266px;
  margin: 0 auto;
  padding: 0 50px;
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}
.main-header {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: flex-start;
  position: relative;
  box-sizing: border-box;
  padding: 0 3.66% 0;
  width: 100vw;
  height: 100vh;
  font-family: "Roboto-Light", "Arial", sans-serif;
  font-size: 1rem;
  line-height: 1.2;
  color: #1b1a17;
  overflow: hidden;
  background-color: #FDFDFB;
}
.main-header__wrapper {
  flex-basis: 24%;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  flex-wrap: wrap;
  padding-top: 2.6875rem;
}
.main-header__logo {
  display: block;
  margin-right: auto;
  width: 8.21%;
}
.main-header__logo svg {
  width: 100%;
  height: auto;
}
.main-header__social {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 5%;
}
.main-header__advertised-product {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  top: 0;
  left: 50%;
  height: 100%;
}
.main-header__advertised-product img {
  position: relative;
  z-index: 2;
  width: 36.3vw;
  height: auto;
}
.main-header__advertised-product span {
  position: absolute;
  top: 19%;
  left: 0;
}
.main-header__advertised-product span::before {
  content: "";
  position: absolute;
  width: 3.2rem;
  height: 1px;
  top: 50%;
  background-color: #8B8371;
}
.main-header__advertised-product span:first-of-type::before {
  right: calc(-100% - 1rem);
}
.main-header__advertised-product span:nth-of-type(2)::before {
  left: -4.2rem;
}
.main-header__advertised-product span:last-child {
  position: absolute;
  top: auto;
  left: auto;
  bottom: 19%;
  right: 0;
}
.main-header__advertised-product::before {
  content: "";
  position: absolute;
  width: 36.31vw;
  transform: translateY(-50%);
  height: 69vw;
  top: 50%;
  right: 34%;
  border-top-left-radius: 100% 50%;
  border-bottom-left-radius: 100% 50%;
  background-image: linear-gradient(180deg, rgba(234, 227, 212, 0) 0%, #EAE3D4 100%);
}
.main-header__content {
  margin-left: 9.51%;
  width: 37%;
}
.main-header__subtitle {
  margin: 0;
  font-family: "PlayfairDisplay-Regular", "Times", serif;
  font-size: 2.8125rem;
  line-height: 1.15;
}
.main-header__subtitle span {
  font-size: 1.875rem;
}
.main-header__title {
  width: 60%;
  margin: 0;
  margin-bottom: 4rem;
  font-size: 0.875rem;
  line-height: 1.3;
}
.main-nav {
  margin-right: 1.5rem;
}
.main-nav ul {
  display: none;
}
.main-nav__toggle {
  border: none;
  background-color: transparent;
  padding: 0;
}
.main-nav__toggle svg {
  width: 1.25rem;
  height: 1.125rem;
}
.user-menu {
  width: 10%;
}
.user-menu__list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0;
}
.user-menu__item--select a {
  position: relative;
  text-decoration: none;
  color: #1B1A17;
  font-size: 0.5rem;
  font-family: "Roboto-Regular", "Arial", sans-serif;
}
.user-menu__item--select div {
  position: absolute;
  min-width: 1rem;
  height: 1rem;
  top: -163%;
  left: 70%;
  background-color: #E6C8B4;
  border-radius: 50%;
  text-align: center;
}
.user-menu__item--select span {
  display: block;
  padding-top: 2px;
}
.social-list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  flex-wrap: wrap;
  list-style: none;
}
.social-list li {
  margin-bottom: 0.8125rem;
}
.social-list li:last-child {
  margin-bottom: 0;
}
.btn {
  display: inline-block;
  padding: 2.17em 3.17em 2.25em 3.17em;
  font-family: "Roboto-Medium", Arial, sans-serif;
  font-weight: 500;
  font-size: 0.75rem;
  line-height: 1.17;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #444240;
  text-decoration: none;
}
